@import url('https://fonts.googleapis.com/css2?family=Fruktur&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Qahiri&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');

*{
    margin:0;
    padding:0;
    box-sizing:border-box;
    font-family: 'Fruktur', cursive;    
}

.shipping{
    position: sticky;
    top: 0;
    background-color: rgb(255,153,0);
    color: white;
    font-weight: bolder;
    padding: 5px;
}

.shipping p{    
    animation: animi 10s linear infinite;
    font-family: 'Source Sans Pro', sans-serif;
}

@keyframes animi{
    100%{
        transform: translateX(1450px);
    }
}

.full_content{
    background-image: url(https://i.pinimg.com/originals/99/6f/bf/996fbf961d4d46d535463d0532a430cd.jpg);
    height: 100vh;
    background-size: cover;    
}

.navigationn_bar{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navigationn_bar ul li{
    display: inline-block;
    margin: 20px 35px;
}

.navigationn_bar ul li a{    
    text-decoration: none;
    color: white;
    font-size: 18px;
}

.navigationn_bar ul li a:hover{    
    border-bottom: 4px solid brown;    
}

.navigation_brand{
    padding: 0 20px;
    letter-spacing: 0.1cm;
    font-size: 2.5rem;
    text-shadow: 4px 4px 3px rgb(6, 95, 117);
    color: white;
}

.navigation_brand span{
    font-size: 1.5rem;
    color: rgb(189, 130, 22);
}

.welcome{
    height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.welcome a{
    font-size: 30px;
    color: blanchedalmond;
}

.feedback{
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}

.feedback p{
    width: 70%;
    font-size: 2.4rem;
    text-align: justify;
    font-family: 'Qahiri', sans-serif;
    color: black;
}

.feedback form{
    margin-right: 100px;
}

.feedback form{
    color: brown;
}

.feedback form input{
    margin: 20px 0px;
    height: 30px;
    width: 250px;
}

.feedback form textarea{
    margin: 20px 0px;
    width: 250px;
}

.brands{
    margin: 30px 0;
    padding: 30px 10px;
}

.brands h1{
    text-align: center;
    font-size: 50px;
}

.images{
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-auto-rows: 300px;
    grid-column-gap: 20px;
    grid-row-gap: 50px;
    padding: 20px 5px;
    /* background-color: rgb(230,231,232); */
    margin: 20px 0;
}

.bike{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.our_products h1{
    text-align: center;
    font-size: 50px;
}

.product_images{
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-auto-rows: 300px;
    grid-column-gap: 10px;
    grid-row-gap: 20px;
    padding: 20px 5px;
    margin: 20px 0;
}

.products{
    width: 100%;
    height: 100%;
    object-fit: contain;
    opacity: 0.8;
    transition: 0.7s;
}

.products:hover{
    opacity: 1;
}

.footer{
    margin-top: 100px;
    padding: 40px 20px;
    height: 250px;
    background-color: #18191A;
    color: white;        
}

.footer .news_letter{
    font-size: 20px;
    text-transform: uppercase;
    font-family: 'Source Sans Pro', sans-serif;
}

.footer .subscribe{
    font-size: 18px;
    font-family: 'Source Sans Pro', sans-serif;
}

.footer .social_media a{
    margin: 0 10px;
    color: white;
    font-family: 'Source Sans Pro', sans-serif;    
}

.footer .contacts a{
    margin: 0 10px;
    color: white;
    text-decoration: none;
    font-family: 'Source Sans Pro', sans-serif;     
}

.scrolling{
    background-color: tomato;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: white;
    position: fixed;
    right: 10px;
    bottom: 10px;
    z-index: 1;
    cursor: pointer;
}

.scrolling a{
    text-decoration: none;
}
